.tabs {
    margin-top: 50px;
    padding-bottom: 40px;
    width: 1000px;
    margin: 0 auto;}
  
  /*ã¿ãã®ã¹ã¿ã¤ã«*/
  .tab_item {
    width: calc(100%/3);
    height: 50px;
    border-bottom: 3px solid #5ab4bd;
    background-color: #f3f2f2;
    line-height: 50px;
    font-size: 16px;
    text-align: center;
    color: #565656;
    display: block;
    float: left;
    text-align: center;
    font-weight: bold;
    transition: all 0.1s ease;
  }
  .tab_item:hover {
    opacity: 0.75;
  }
  
  /*ã©ã¸ãªãã¿ã³ãå¨ã¦æ¶ã*/
